.entrer{
    position: absolute;
    top: 40%;
    left: 50%;
    color: rgb(126, 126, 126);
    font-size: 3em;
        font-family: Georgia, 'Times New Roman', Times, serif;
        -webkit-transition: 1s;
        transition: 1s;
}

.entrer:hover{
    color: rgb(126, 126, 126);
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 4em;
    text-decoration: none;
}
.imageHome{
    width: 100%;
    margin: auto;
    height: 700px;
}
.AddProductTrue{
    -webkit-transition: 1s ;
    transition: 1s ;
    position: fixed;
    top: 15px;
    width: 30%;
    opacity: 0.7;
}

.AddProductFalse{
  -webkit-transition: 1s ;
  transition: 1s ;
}

.elementIn{
